import { Suppliers as sp } from '../Actions/Types';

const initialState = {
    data : ''
};

const Suppliers = (state = initialState, action:any) => {
	switch (action.type) {
    	case sp:
      		return {
        		data :  action.payload,
            }
    	default:
      		return state;
 	}
}

export default Suppliers;
