import { Paper, Table, TableBody, TableRow, TableCell, TableFooter, TablePagination, TableHead, Grid, Button, Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, TextField, CircularProgress } from '@material-ui/core';
import TablePaginationActions from '@material-ui/core/TablePagination/TablePaginationActions';
import { Products as productRedux, Suppliers as supplierRedux} from 'Redux/Actions';
import { withRouter } from 'react-router-dom';
import Skeleton from '@material-ui/lab/Skeleton';
import './ProductsStyle.scss';
import { Close, Edit, Save, Cancel, SaveAlt, CloudUpload } from '@material-ui/icons';
import Select from 'react-select';
import moment from 'moment';
import { DatePicker } from "material-ui-pickers";
import { Requests } from '../../../Services';
import { useSnackbar } from 'notistack';
