    const updateInput = (event:any, value?:any) => {

        if(event){
            if(event instanceof Date){
                setProductInput({
                    ...productInput,
                    [value] : moment(event).format('YYYY,MM,DD'),
                })
            }else{
                setProductInput({
                    ...productInput,
                    [event.target.name] : event.target.value,
                })
            }
        }
    }


