import { NavigationTitle as NT } from '../Actions/Types';

const initialState = {
    title : '',
};

const NavigationTitle = (state = initialState, action:any) =>{
	switch (action.type) {
    	case NT:
      		return {
        		title :  action.payload,
            }
    	default:
      		return state;
 	}
}

export default NavigationTitle;