import React from 'react';
import Navigations from 'Routes';
import { Provider } from 'react-redux';
import store from './Redux/Store';
import { SnackbarProvider } from 'notistack';
const App = () => {
    return(
        <Provider store={store}>
            <SnackbarProvider 
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                maxSnack={10}
                autoHideDuration ={4000}
            >
                <Navigations />
            </SnackbarProvider>
        </Provider>
    );
}

export default App;
