import { TableRow, TableCell, Popper, Paper, Typography, TextField, Button } from "@material-ui/core"
import React, { useState } from "react";
import { DatePicker } from "material-ui-pickers";
import moment from "moment";
import {Filter as Filterer} from "Redux/Actions";
import {useDispatch} from 'react-redux';


const Filter = () => {



    const dispatch = useDispatch();

    type control = "supplier_code" | "supplier_name" | "address" | "contact_person" | "contact_number" | "email";
    type action = "close" | "open";
    type submit = "clear" | "filter";


    const [popper,setPopper] = useState({
        supplier_code       : null,
        supplier_name       : null,
        address             : null,
        contact_person      : null,
        contact_number      : null,
        email               : null,
    });

    const [filter, setFilter] = useState({
        supplier_code       : "",
        supplier_name       : "",
        address             : "",
        contact_person      : "",
        contact_number      : "",
        email               : "",
    });


    const reset = () => {
        
        let a = popper;
        a.supplier_code       = null;
        a.supplier_name       = null;
        a.address             = null;
        a.contact_person      = null;
        a.contact_number      = null;
        a.email               = null;
        setPopper(a);
    }
    
    const controller = (event:any,control:control,action:action) => {

        reset();
        
        if(action === "open"){
            setPopper({
                ...popper,
                [control] : event.currentTarget,
            })
        }else{
            setPopper({
                ...popper,
                [control] : null,
            })
        }
    }

    const setText=(event:any) =>{
        setFilter({
            ...filter,
            [event.target.name] : event.target.value,
        });

        console.log(filter);
    }

    const submitFilter = async (action:submit,control:control) =>{
        reset();
        if(action === "filter"){
            if(filter[control] !== ''){
                dispatch(Filterer(filter,"supplier"));
            }
        }else{

            let a = filter;

            setFilter({
                ...filter,
                [control] : '',
            });

            console.log(filter,control);
            
            a[control] = "";
            dispatch(Filterer(a,"supplier"));
        }
    }

    const onKey = (e:any) => {

        if(e.key === "Backspace"){
            if(e.target.value.length === 1){
                reset();
                dispatch(Filterer(filter,"supplier"));
            }
        }

        if(e.key === 'Enter'){
            if(e.target.value !== ''){
                reset();
                dispatch(Filterer(filter,"supplier"));
            }
        }
    }


    return(
        <TableRow>
            <TableCell align="center" className="filter">
                <TextField
                    id="outlined-email-input"
                    className="input"
                    autoComplete="off"
                    margin="normal"
                    variant="outlined"
                    name = "supplier_code"
                    value = {filter.supplier_code}
                    onChange = {setText}
                    onFocus = {(event)=>controller(event,"supplier_code","open")}
                    onKeyDown = {onKey}
                />
                
                <Popper className="text-center" id={Boolean(popper.supplier_code)? "simple-popper" : undefined} open={Boolean(popper.supplier_code)} anchorEl={popper.supplier_code}>
                    <Paper className="popper-paper">
                        <Button variant="contained" color="primary" onClick={()=>submitFilter("clear","supplier_code")}>
                            Clear
                        </Button>
                        <Button variant="contained" color="secondary" onClick={()=>submitFilter("filter","supplier_code")}>
                            Filter
                        </Button>
                    </Paper>
                </Popper>

            </TableCell>

            <TableCell align="center" className="filter">
                
                <TextField
                    id="outlined-email-input"
                    className="input"
                    autoComplete="off"
                    margin="normal"
                    variant="outlined"
                    name = "supplier_name"
                    value = {filter.supplier_name}
                    onChange = {setText}
                    onFocus = {(event)=>controller(event,"supplier_name","open")}
                    onKeyDown = {onKey}
                />
                
                <Popper className="text-center" id={Boolean(popper.supplier_name)? "simple-popper" : undefined} open={Boolean(popper.supplier_name)} anchorEl={popper.supplier_name}>
                    <Paper className="popper-paper">
                        <Button variant="contained" color="primary" onClick={()=>submitFilter("clear","supplier_name")}>
                            Clear
                        </Button>
                        <Button variant="contained" color="secondary" onClick={()=>submitFilter("filter","supplier_name")}>
                            Filter
                        </Button>
                    </Paper>
                </Popper>

            </TableCell>
            <TableCell align="center" className="filter">
                <TextField
                    id="outlined-email-input"
                    className="input"
                    autoComplete="off"
                    margin="normal"
                    variant="outlined"
                    name = "address"
                    value = {filter.address}
                    onChange = {setText}
                    onFocus = {(event)=>controller(event,"address","open")}
                    onKeyDown = {onKey}
                />
                
                <Popper className="text-center" id={Boolean(popper.address)? "simple-popper" : undefined} open={Boolean(popper.address)} anchorEl={popper.address}>
                    <Paper className="popper-paper">
                        <Button variant="contained" color="primary" onClick={()=>submitFilter("clear","address")}>
                            Clear
                        </Button>
                        <Button variant="contained" color="secondary" onClick={()=>submitFilter("filter","address")}>
                            Filter
                        </Button>
                    </Paper>
                </Popper>
            </TableCell>
            <TableCell align="center" className="filter">
                <TextField
                    id="outlined-email-input"
                    className="input"
                    autoComplete="off"
                    margin="normal"
                    variant="outlined"
                    name = "serial_number"
                    value = {filter.contact_person}
                    onChange = {setText}
                    onFocus = {(event)=>controller(event,"contact_person","open")}
                    onKeyDown = {onKey}
                />
                
                <Popper className="text-center" id={Boolean(popper.contact_person)? "simple-popper" : undefined} open={Boolean(popper.contact_person)} anchorEl={popper.contact_person}>
                    <Paper className="popper-paper">
                        <Button variant="contained" color="primary" onClick={()=>submitFilter("clear","contact_person")}>
                            Clear
                        </Button>
                        <Button variant="contained" color="secondary" onClick={()=>submitFilter("filter","contact_person")}>
                            Filter
                        </Button>
                    </Paper>
                </Popper>
            </TableCell>
            <TableCell align="center" className="filter">
                <TextField
                    id="outlined-email-input"
                    className="input"
                    autoComplete="off"
                    margin="normal"
                    variant="outlined"
                    name = "contact_number"
                    value = {filter.contact_number}
                    onChange = {setText}
                    onFocus = {(event)=>controller(event,"contact_number","open")}
                    onKeyDown = {onKey}
                />
                
                <Popper className="text-center" id={Boolean(popper.contact_number)? "simple-popper" : undefined} open={Boolean(popper.contact_number)} anchorEl={popper.contact_number}>
                    <Paper className="popper-paper">
                        <Button variant="contained" color="primary" onClick={()=>submitFilter("clear","contact_number")}>
                            Clear
                        </Button>
                        <Button variant="contained" color="secondary" onClick={()=>submitFilter("filter","contact_number")}>
                            Filter
                        </Button>
                    </Paper>
                </Popper>
            </TableCell>
            <TableCell align="center" className="filter">
                <TextField
                    id="outlined-email-input"
                    className="input"
                    autoComplete="off"
                    margin="normal"
                    variant="outlined"
                    name = "email"
                    value = {filter.contact_number}
                    onChange = {setText}
                    onFocus = {(event)=>controller(event,"email","open")}
                    onKeyDown = {onKey}
                />
                
                <Popper className="text-center" id={Boolean(popper.email)? "simple-popper" : undefined} open={Boolean(popper.email)} anchorEl={popper.email}>
                    <Paper className="popper-paper">
                        <Button variant="contained" color="primary" onClick={()=>submitFilter("clear","email")}>
                            Clear
                        </Button>
                        <Button variant="contained" color="secondary" onClick={()=>submitFilter("filter","email")}>
                            Filter
                        </Button>
                    </Paper>
                </Popper>
            </TableCell>
        </TableRow>
    );
}

export default Filter;