    static async update(product:Products){
        
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }

        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }
        
        const header = this.header(Token.get());
        
        await axios({
            method  :   "PATCH",
            url     :   Url.products+product.id,
            headers :   header,
            params  :   product
        })
        .then( (response:any) => {
            format.status = response.status; 
            format.data = response.data;
        }).catch( async (error) =>{
            format = await this.handle_error({
                response    : error.response,
                format      : format,
                callback    : this.update,
                auth        : false,
                params      : product
            });
          
        })
        return format;
    }

    // static async patch(user:Update){

