    static async add(product:Products){
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }

        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }
        
        const header = this.header(Token.get());
        
        await axios({
            method  :   "POST",
            url     :   Url.products,
            headers :   header,
            params  :   product
        })
        .then( (response:any) => {
            format.status = response.status; 
            format.data = response.data;
        }).catch( async (error) =>{
            format = await this.handle_error({
                response    : error.response,
                format      : format,
                callback    : this.add,
                auth        : false,
                params      : product
            });
          
        })
        return format;
    }


    static async addFile(file:File){
        
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }

        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }
        
        const token = Token.get();
        const formData = new FormData();
        formData.append('file',file);

        await axios.post(Url.products,formData,{
            headers: {
                'Content-Type'         :    'application/x-www-form-urlencoded',
                'Accept'               :    'application/json',
                'Authorization'        :    'Bearer '+token
            }})
        .then( (response:any) => {
            format.status = response.status; 
            format.data = response.data;
        }).catch( async (error) =>{
            format = await this.handle_error({
                response    : error.response,
                format      : format,
                callback    : this.addFile,
                auth        : false,
                params      : file
            });
          
        })
        return format;
    }

    static async search(search:string){
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }
        
        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }
        
        const header = this.header(Token.get());
        
        await axios({
            method  :   "GET",
            url     :   Url.products+'?search='+search,
            headers :   header,
        })
        .then(this.sleeper(1000))
        .then( (response:any) => {
            format.status = response.status;
            format.data = response.data;
        }).catch( async (error) =>{
            format = await this.handle_error({
                response    : error.response,
                format      : format,
                callback    : this.show,
                auth        : false,
                params      : search
            });
        })
        return format;
    }


    static async filter(filter:any){

        let params = new URLSearchParams(filter).toString();

        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }
        
        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }
        
        const header = this.header(Token.get());
        
        await axios({
            method  :   "GET",
            url     :   Url.products+'?filter=true&'+params,
            headers :   header,
        })
        .then(this.sleeper(1000))
        .then( (response:any) => {
            format.status = response.status;
            format.data = response.data;
        }).catch( async (error) =>{
            format = await this.handle_error({
                response    : error.response,
                format      : format,
                callback    : this.show,
                auth        : false,
                params      : filter
            });
        })
        return format;
    }

