import {StaticMethods, Format} from './StaticMethods';
import Token from "../Token";
import Url from '../ServerUrl';
import axios from 'axios';
import { userInfo } from 'os';


interface Show{
    page        : number,
    per_page    : number
}



interface Users{
    id         : any,
    first_name : string,
    last_name  : string,
    username   : string,
    company    : string,
    email      : string,
    activated  : any,
    level      : any,
}



class User extends StaticMethods{

    static async show(user?:Show){
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }

        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }

        const header = this.header(Token.get());

        await axios({
            method  :   "GET",
            url     :   Url.user+'?page='+user.page+'&per_page='+user.per_page,
            headers :   header,
        }).then( response => {
            format.status = response.status;
            format.data = response.data;
        }).catch( async (error) =>{
            format = await this.handle_error({
                response : error.response,
                format : format,
                callback : this.get,
                auth    : false,
                params : user,
            });
        })
        return format;
    }

    static async get(id:number){
        
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }

        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }

        const header = this.header(Token.get());

        await axios({
            method  :   "GET",
            url     :   Url.user+id,
            headers :   header,
        }).then( response => {
            format.status = response.status;
            format.data = response.data.data;
        }).catch( async (error) =>{
            format = await this.handle_error({
                response : error.response,
                format : format,
                callback : this.get,
                auth    : false,
                params : id
            });
        })
        return format;

    }

    static async update(user:Users){
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }

        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }

        const header = this.header(Token.get());

        await axios({
            method  :   "PATCH",
            url     :   Url.user+user.id,
            headers :   header,
            params  :   user
        }).then( response => {
            format.status = response.status;
            format.data = response.data;
        }).catch( async (error) =>{
            format = await this.handle_error({
                response : error.response,
                format : format,
                callback : this.update,
                auth    : false,
                params  : user,
            });
        })
        return format;
    }

    static async add(user:Users){
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }

        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }

        const header = this.header(Token.get());

        await axios({
            method  :   "POST",
            url     :   Url.user,
            headers :   header,
            params  :   user
        }).then( response => {
            format.status = response.status;
            format.data = response.data;
        }).catch( async (error) =>{
            format = await this.handle_error({
                response    : error.response,
                format      : format,
                callback    : this.add,
                auth        : false,
                params      : user,
            });
        })
        return format;
    }

    static async search(search:string){
        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }

        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }

        const header = this.header(Token.get());

        await axios({
            method  :   "GET",
            url     :   Url.user+'?search='+search,
            headers :   header,
        }).then( response => {
            format.status = response.status;
            format.data = response.data;
        }).catch( async (error) =>{
            format = await this.handle_error({
                response : error.response,
                format : format,
                callback : this.get,
                auth    : false,
                params  : search,
            });
        })
        return format;
    }


    static async filter(filter:any){

        let params = new URLSearchParams(filter).toString();


        let format:Format = {
            network_error : false,
            status        : 0,
            data          : '',
        }

        if(!Token.exist()){
            format.status = 401;
            format.data = 'Cookies Not Found';
            return format;
        }

        const header = this.header(Token.get());

        await axios({
            method  :   "GET",
            url     :   Url.user+'?filter=true&'+params,
            headers :   header,
        }).then( response => {
            format.status = response.status;
            format.data = response.data;
        }).catch( async (error) =>{
            format = await this.handle_error({
                response : error.response,
                format : format,
                callback : this.get,
                auth    : false,
                params  : filter,
            });
        })
        return format;
    }

}

export default User;