import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { NavigationTitle, Search } from 'Redux/Actions';
import { AssignmentInd, Assessment, AssignmentTurnedIn, Assignment, SupervisorAccount, Settings } from '@material-ui/icons';
import './HomeStyle.scss';
import { Button } from '@material-ui/core';
import { withRouter } from 'react-router-dom';


const Home = (props:any) =>{
    const dispatch = useDispatch();
    const user = useSelector( (state:any) => state.UserAccount.data);

    useEffect( () =>{
        dispatch(NavigationTitle('Home'));
        
        return () => {
            dispatch(NavigationTitle(''));
        }

    },[dispatch])
    return(
        <div className="mx-auto" style={{marginTop:'100px'}}>
            { user.level === 1 ?
                <React.Fragment>
                    <div className="justify-content-center row">
                        <div className="col-sm text-center iconButton" onClick={ () => props.history.push('/suppliers')}>
                            <div className="col"><AssignmentInd className="home-icon" /></div>
                            <div className="col">Suppliers</div>
                        </div>
                        <div className="col-sm text-center iconButton" onClick={ () => props.history.push('/products')}>
                            <div className="col"><Assignment className="home-icon" /></div>
                            <div className="col">Products</div>
                        </div>
                        <div className="col-sm text-center iconButton" onClick={ () => props.history.push('/user-logs')}>
                            <div className="col"><AssignmentTurnedIn className="home-icon" /></div>
                            <div className="col">User Logs</div>
                        </div>
                    </div>
                    <div className="justify-content-center row">
                        <div className="col-sm text-center iconButton" onClick={ () => props.history.push('/reports')}>
                            <div className="col"><Assessment className="home-icon"/></div>
                            <div className="col">Reports</div>
                        </div>
                        <div className="col-sm text-center iconButton" onClick={ () => props.history.push('/users')}>
                            <div className="col"><SupervisorAccount className="home-icon" /></div>
                            <div className="col">Users</div>
                        </div>
                    
                        <div className="col text-center iconButton"  onClick={ () => props.history.push('/settings')}>
                            <div className="col"><Settings className="home-icon" /></div>
                            <div className="col">Settings</div>
                        </div>
                    </div>
                </React.Fragment>
            :
                <React.Fragment>
                    <div className="justify-content-center row">
                        <div className="col-sm text-center iconButton" onClick={ () => props.history.push('/suppliers')}>
                            <div className="col"><AssignmentInd className="home-icon" /></div>
                            <div className="col">Suppliers</div>
                        </div>
                        <div className="col-sm text-center iconButton" onClick={ () => props.history.push('/products')}>
                            <div className="col"><Assignment className="home-icon" /></div>
                            <div className="col">Products</div>
                        </div>
                    </div>
                    <div className="justify-content-center row">
                        <div className="col-sm text-center iconButton" onClick={ () => props.history.push('/reports')}>
                            <div className="col"><Assessment className="home-icon"/></div>
                            <div className="col">Reports</div>
                        </div>
                        <div className="col-sm text-center iconButton" onClick={ () => props.history.push('/profile')}>
                            <div className="col"><SupervisorAccount className="home-icon" /></div>
                            <div className="col">Profile</div>
                        </div>
                    </div>
                </React.Fragment>
            }
        </div>
    )
}

export default withRouter(Home);