import { Suppliers as sp, Products as pr, Users as us } from './Types';
import { Requests } from '../../Services';

type component = "supplier" | "product" | "user";

export const Filter = (filter:any,component:component) => {

        switch(component){
            case "supplier" :
                return async (dispatch:any) => {

                    dispatch({
                        type    : sp,
                        payload : '',
                    });
        
    
                    dispatch({
                        type    : sp,
                        payload : await Requests.Suppliers.filter(filter),
                    });
                }
            case "product" :
                return async (dispatch:any) => {

                    dispatch({
                        type    : pr,
                        payload : '',
                    });
        
    
                    dispatch({
                        type    : pr,
                        payload : await Requests.Products.filter(filter),
                    });
                }
            case "user" :
                return async (dispatch:any) => {

                    dispatch({
                        type    : us,
                        payload : '',
                    });
        
    
                    dispatch({
                        type    : us,
                        payload : await Requests.User.filter(filter),
                    });
                }
        }

        
}
