import { Products as pr } from '../Actions/Types';

const initialState = {
    data : ''
};

const Products = (state = initialState, action:any) => {
	
	switch (action.type) {
    	case pr:
      		return {
        		data : action.payload,
            }
    	default:
			// console.log(action,state);
      		return state;
 	}
}

export default Products;
