import { Suppliers as sp } from './Types';
import { Requests } from '../../Services';

interface params{
    page        :  number,
    per_page    :  number,
}
export const Suppliers = (params ?:params) => {
        return async (dispatch:any) => {

            dispatch({
                type    : sp,
                payload : '',
            });

            if(!params){
                params.page     = 1;
                params.per_page = 10;
            }

            dispatch({
                type    : sp,
                payload : await Requests.Suppliers.show(params),
            });
        }
}
