import { Products as pr } from './Types';
import { Requests } from '../../Services';

interface params{
    page        :  number,
    per_page    :  number,
}
export const Products = (params ?:params) => {
        return async (dispatch:any) => {

            if(!params){
                params.page     = 1;
                params.per_page = 10;
            }

            dispatch({
                type    : pr,
                payload : '',
            });



            await Requests.Products.show(params).then((response)=>dispatch({
                type    : pr,
                payload : response,
            }));

        }
}
